.user-data {
    position: relative;
  
    .planet-img-user-page {
      position: absolute;
      right: -300px;
      top: -50px;
    }
  
    h2 {
      margin-top: 110px;
      margin-bottom: 45px;
      font-style: normal;
      font-weight: 700;
      font-size: 48px;
      line-height: 120%;
      color: #ffffff;
    }
  
    .block-of-data {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
  
      .data-subtitle {
        margin-bottom: 10px;
      }
  
      h3 {
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 120%;
        color: #e75626;
      }
    }
  }
  